import { Component, OnInit } from '@angular/core'
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { Lembur_pengaturan } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { KalenderLemburPengaturanService } from 'src/app/sdk/hr/services';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss'],
})
export class AddComponent implements OnInit {
    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private kalenderLemburPengaturanService: KalenderLemburPengaturanService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }


    ngOnInit() {
        this.form();
    }

    komponen: FormGroupTypeSafe<Lembur_pengaturan>;
    form() {
        this.komponen = this.fb.group<Lembur_pengaturan>(
            {
                kode_lembur: new FormControl('', [Validators.required]),
                lembur_pengaturan: new FormControl('', [Validators.required])
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.komponen.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.komponen.invalid) {
            console.log(this.komponen.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.kalenderLemburPengaturanService.postKalenderLemburPengaturans(this.komponen.value).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/kalender/pengaturan/masterlembur/list'])
    }

}
