import { Component, OnInit } from '@angular/core'
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { KalenderLemburPengaturanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Lembur_pengaturan } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss'],
})
export class EditComponent implements OnInit {
    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private kalenderLemburPengaturanService: KalenderLemburPengaturanService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }


    id: string = ''
    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id_lembur_pengaturan')
        this.form();
        this.getData()
    }

    getData() {
        this.kalenderLemburPengaturanService.getKalenderLemburPengaturansId(this.id).subscribe(
            data => {
                this.komponen.setValue(data.data)
            }
        )
    }

    komponen: FormGroupTypeSafe<Lembur_pengaturan>;
    form() {
        this.komponen = this.fb.group<Lembur_pengaturan>(
            {
                id_lembur_pengaturan: new FormControl('', [Validators.required]),
                kode_lembur: new FormControl('', [Validators.required]),
                lembur_pengaturan: new FormControl('', [Validators.required]),
                nilai: new FormControl(''),
                custum_rumus: new FormControl(''),
                keterangan: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.komponen.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.komponen.invalid) {
            console.log(this.komponen.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.kalenderLemburPengaturanService.putKalenderLemburPengaturansId({ id: this.id, body: this.komponen.value }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/kalender/pengaturan/masterlembur/list'])
    }
}
