import { Component, OnInit } from '@angular/core'
import { FilterFuctionService } from 'src/app/services/filter-fuction.service'
import { NzMessageService } from 'ng-zorro-antd'
import { NgxSpinnerService } from 'ngx-spinner'
import { KalenderLemburPengaturanService } from 'src/app/sdk/hr/services/kalender-lembur-pengaturan.service'
import { Lembur_pengaturan } from 'src/app/sdk/hr/models/lembur-_pengaturan'

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss'],
})
export class ListComponent implements OnInit {
    constructor(
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
        private kalenderLemburPengaturanService: KalenderLemburPengaturanService
    ) { }

    ngOnInit() {
        this.getData()
    }

    listOfDisplayData: Lembur_pengaturan[] = []
    listOfData: Lembur_pengaturan[] = []
    getData() {
        this.kalenderLemburPengaturanService.getKalenderLemburPengaturans(JSON.stringify({sortKey: this.sortKey, sortValue: this.sortValue, search: this.search})).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
            }
        )
    }


    sortKey: string | null = '';
    sortValue: string | null = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.getData();
    }
    search: string = "";



    delete(id) {
        this.spinner.show();
        this.kalenderLemburPengaturanService.deleteKalenderLemburPengaturansId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }
}
